.loader {
  height: 70px;
  width: 70px;
  margin-left: 140px;
  animation: rotate 2s infinite;
  display: none;
}

.loader.display {
  display: block;
}

.loader:before, .loader:after {
  content: "";
  height: 20px;
  width: 20px;
  border-radius: 50%;
  display: block;
}

.loader:before {
  background-color: gold;
  margin-bottom: 10px;
  animation: ball1 1s infinite;
  box-shadow: 30px 0 #0057b7;
}

.loader:after {
  background-color: #0057b7;
  animation: ball2 1s infinite;
  box-shadow: 30px 0 gold;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg)scale(.8);
  }

  50% {
    transform: rotate(360deg)scale(1.2);
  }

  100% {
    transform: rotate(720deg)scale(.8);
  }
}

@keyframes ball1 {
  0% {
    box-shadow: 30px 0 #0057b7;
  }

  50% {
    margin-bottom: 0;
    transform: translate(15px, 15px);
    box-shadow: 0 0 #0057b7;
  }

  100% {
    margin-bottom: 10px;
    box-shadow: 30px 0 #0057b7;
  }
}

@keyframes ball2 {
  0% {
    box-shadow: 30px 0 gold;
  }

  50% {
    margin-top: -20px;
    transform: translate(15px, 15px);
    box-shadow: 0 0 gold;
  }

  100% {
    margin-top: 0;
    box-shadow: 30px 0 gold;
  }
}

